import { useContext, useEffect, useState } from "react";
import axios from "axios";
import DataContext from "../usecontext/DataContext";

export default function Platform1(params) {
  const apiKey = "https://backend-mqr6.onrender.com";
  const { userrole } = useContext(DataContext);
  const { platformdata, fetchplatform } = useContext(DataContext);

  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [editingId, setEditingId] = useState(null);
  const [newName, setNewName] = useState('');

  useEffect(() => {
    fetchplatform();
  }, [fetchplatform]);

  const handleEditClick = (person) => {
    setEditingId(person._id);
    setNewName(person.name); // Pre-fill the input with the current name
  };

  const handleInputChange = (e) => {
    setNewName(e.target.value);
  };

  const handleEditSubmit = async (id) => {
    try {
      const token = localStorage.getItem("token");
      const headers = {
        'authtoken': token
      };

      const response = await axios.put(`${apiKey}/api/Platform/updateplatform/${id}`, {
        name: newName,
      }, { headers });

      if (response.status === 200) {
        setData(prevData =>
          prevData.map(platform =>
            platform._id === id ? { ...platform, name: newName } : platform
          )
        );
        setEditingId(null);
        fetchplatform();
        setNewName('');
      } else {
        console.error('Failed to update platform:', response.status);
      }
    } catch (error) {
      console.error('Error updating platform:', error);
    }
  };

  const deleteProduct = async (id) => {
    const token = localStorage.getItem("token");
    try {
      await axios.delete(`${apiKey}/api/Platform/deleteplatform/${id}`, {
        headers: {
          'authtoken': token
        },
      });
      fetchplatform();
    } catch (error) {
      console.error('Error deleting platform:', error);
    }
  };

  const confirmDelete = (id) => {
    if (window.confirm("Do you really want to delete?")) {
      deleteProduct(id);
    }
  };

  if (error) return <p>Error: {error.message}</p>;
  if (!platformdata) {
    return <div className="flex justify-center items-center min-h-screen">Loading...</div>;
  }

  return (
    <div className="p-4 space-y-5  h-[500px] overflow-y-scroll">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {platformdata?.map((person) => (
          <div
            key={person._id}
            className="relative bg-gradient-to-r from-purple-400 via-pink-500 to-red-500 p-6 rounded-lg shadow-2xl transform transition-transform duration-500 hover:scale-105 hover:shadow-2xl"
          >
            <div className="flex flex-row items-start space-y-4">
              {userrole.canEdit && editingId === person._id ? (
                <div className="w-full flex items-center space-x-2">
                  <input
                    type="text"
                    value={newName}
                    onChange={handleInputChange}
                    className="border-gray-300 px-3 py-2 font-semibold text-center border-b-2 bg-white text-black rounded-md"
                  />
                  <button
                    onClick={() => handleEditSubmit(person._id)}
                    className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-1 px-2 rounded-md"
                  >
                    Save
                  </button>
                </div>
              ) : (
                <div className="flex justify-between items-center flex-col w-full">
                  <span className="font-semibold text-lg text-white">{person.name}</span>
                  {userrole.canEdit && (
                    <button
                      onClick={() => handleEditClick(person)}
                      className="bg-yellow-500 hover:bg-yellow-600 text-white font-bold py-1 px-2 rounded-md"
                    >
                      Edit
                    </button>
                  )}
                </div>
              )}
              {userrole.canDelete && (
                <button
                  onClick={() => confirmDelete(person._id)}
                  className="absolute bottom-2 right-2 bg-red-600 hover:bg-red-700 text-white font-bold py-1 px-3 rounded-md"
                >
                  Delete
                </button>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
